body {
  margin: 0;
  font-family: 'Muli';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* for plots */

.plot-title {
  font-size: 12px;
  font-weight: 700;
  fill: white;
}

.plot-title-strip {
  fill: rgb(0, 0, 0, 0.5);
}

.x-lab {
  font-size: 12px;
  font-weight: 700;
}

.background-line {
  fill: none;
  stroke-width: 1;
}

.selected-line {
  fill: none;
  stroke-width: 2;
}

.selected-ci {
  stroke: none;
  stroke-width: 1;
  stroke-opacity: 0.25;
  fill-opacity: 0.25;
  pointer-events: none;
}

.light-stroke {
  stroke: #f5f5f5;
}

.light-stroke-grp0 {
  stroke: #f8dddd;
}

.light-stroke-grp1 {
  stroke: #dae4ee;
}

.light-stroke-lrr0 {
  stroke: #fce8d4;
}

.light-stroke-lrr1 {
  stroke: #ddedda;
}

.light-stroke-lrr2 {
  stroke: #e3f0ef;
}

.full-stroke {
  stroke: black;
}

.full-stroke-grp0 {
  stroke: #E15759;
}

.full-stroke-grp1 {
  stroke: #4E79A7;
}

.full-stroke-lrr0 {
  stroke: #F28E2B;
}

.full-stroke-lrr1 {
  stroke: #59A14F;
}

.full-stroke-lrr2 {
  stroke: #76B7B2;
}


.light-fill {
  fill: #f5f5f5;
}

.light-fill-grp0 {
  fill: #f8dddd;
}

.light-fill-grp1 {
  fill: #dae4ee;
}

.light-fill-lrr0 {
  fill: #fce8d4;
}

.light-fill-lrr1 {
  fill: #ddedda;
}

.light-fill-lrr2 {
  fill: #e3f0ef;
}

.full-fill {
  fill: black;
}

.full-fill-grp0 {
  fill: #E15759;
}

.full-fill-grp1 {
  fill: #4E79A7;
}

.full-fill-lrr0 {
  fill: #F28E2B;
}

.full-fill-lrr1 {
  fill: #59A14F;
}

.full-fill-lrr2 {
  fill: #76B7B2;
}

.envelope {
  fill: rgba(246, 246, 246);
}

.tooltip {
  z-index: 10000;
  background: red;
  position: absolute;
}

